rder: 1px solid #ff0000;\r\n    background: rgba(0, 0, 0, 0.4);\r\n}\r\n\r\n.container-info {\r\n    opacity: 0;\r\n}\r\n\r\n.content:hover .addinfohover {\r\n    opacity: 1;\r\n}\r\n\r\n.content:hover .container {\r\n    opacity: 0;\r\n}\r\n\r\n.content:hover .container-info {\r\n    opacity: 1;\r\n    -webkit-animation-name: fadeInUp;\r\n            animation-name: fadeInUp;\r\n    -webkit-animation-duration: 0.3s;\r\n            animation-duration: 0.3s;\r\n}\r\n\r\n.container-info {\r\n    opacity: 0;\r\n}\r\n\r\n.top {\r\n    box-shadow: inset 0px 0px 60px -11px #888, inset 0px 0px 60px -11px #888;\r\n}\r\n\r\n.bottom {\r\n    box-shadow: 0 5px 5px -5px #333;\r\n}\r\n\r\n/* HELPERS LINES */\r\n\r\n/* .helperline-red {\r\n    border: solid red 4px;\r\n}\r\n\r\n.helperline-pink {\r\n    border: solid pink 4px;\r\n}\r\n\r\n.helperline-green {\r\n    border: solid green 4px;\r\n} */\r\n/*# sourceMappingURL=data:application/json;base64, */"